<template>
  <div @click='isActive = !isActive' :class="{ active: isActive}" class="accordion">
    <div class="container">
      <div>
        <span>{{text}}</span>
        <img src="../assets/arrow-ICON.png" alt="arrow-icon">
      </div>
      <div >
        <a
        v-for='item in content'
        :class="{highlight:item.id == selected[item.id]}"
        @click='emitirFiltros(item.name, item.id)'
        :key='item.id'
        href="#"
        >{{ item.name }}</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Accordion',
  data() {
    return {
      isActive: false,
      filtros: [],
      selected: [],
    };
  },
  props: {
    text: String,
    content: Array,
    type: String,
  },
  methods: {
    emitirFiltros(item, id) {
      const ordenar = (this.type === 'ordenar');
      if (!this.filtros.includes(item)) {
        if (ordenar) this.filtros = [];
        this.filtros.push(item);
        this.$emit('emitirFiltros', this.filtros, this.type);
        if (ordenar) {
          this.selected[6] = '';
          this.selected[7] = '';
        }
        this.selected[id] = id;
      } else {
        this.filtros = this.filtros.filter((m) => m !== item);
        this.$emit('emitirFiltros', this.filtros, this.type);
        this.selected[id] = '';
      }
    },
  },
};
</script>

<style lang="scss" scoped>
