    opacity: 1;
    height: 3em;
    width: 100%;
    position: absolute;
    overflow: hidden;
    z-index: 3;
    transition: height .25s;
    div:first-child {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 3em;
      padding: 1.5em;
    }
    div:nth-child(2) {
      opacity: 0;
      transition: opacity .25s;
      font-size: $text-mdsm;
      font-weight: $regular;
      display: flex;
      flex-flow: column;
      top: 100%;
      a {
        padding: 1.5em;
