        &:hover {
          background-color: $color-secondary;
          color: white;
        }
      }
    }
  }
  img {
    height: 24px;
    width: 24px;
    transition: transform .25s;
  }
  span {
    font-size: $text-mdsm;
    font-weight: $bold;
  }
}

.active {
  .container {
    height: initial;
    div:nth-child(2) {
