<template>
  <div class="inicio">
    <Logo class='logoInicio' :only-on-mobile="true"/>
    <FilterModal v-if="launchFilterModal"
    v-on:emitirFiltros='responsiveFilter'
    v-on:emitReturn="launchFilterModal=false"/>

    <div v-if="true" class="nav">
      <SearchBar v-on:emitirFiltros='aplicarFiltros'/>
      <Accordion v-on:emitirFiltros='aplicarFiltros'
        :content="content1"
        type='filtrar'
        text = "Programa de Especializacion"/>
      <Accordion v-on:emitirFiltros='aplicarFiltros'
        :content="content2"
        type='ordenar'
        text = "Ordenar por"/>
      <NavFilter v-on:emitMostrar="launchFilterModal=true"/>
    </div>
    <div v-if="(filterUsers(filters)).length" class="cards-container">
      <Card v-for='user in filterUsers(filters)'
      :key="user.id"
      :id="user.id"
      :picture_Url="user.picture_Url"
      :name="user.name"
      :about="user.about"
      :espe="user.espe"
      :campus="user.campus"
      :phone="user.phone"
      :email="user.email"
      />
    </div>
    <div v-else class="no-cards">
      <h2>No hay nadie registrado a√∫n</h2>
    </div>
  </div>
</template>

<script>
import Card from '@/components/Card.vue';
import SearchBar from '@/components/SearchBar.vue';
import Accordion from '@/components/Accordion.vue';
import NavFilter from '@/components/Filter.vue';
import Logo from '@/components/Logo.vue';
import FilterModal from '@/components/FilterModal.vue';
import { mapState, mapActions, mapGetters } from 'vuex';


export default {
  name: 'Inicio',
  data() {
    return {
      content1: [{ name: 'Front-End', id: 1 }, { name: 'Back-End', id: 2 }, { name: 'Desarrollo de APPS', id: 3 }, { name: 'Dise√±o UX', id: 4 }, { name: 'Desarrollo de Videojuegos en Unity', id: 5 }],
      content2: [{ name: 'A - Z', id: 6 }, { name: 'Z - A', id: 7 }],
      filters: [[], [], []],
      launchFilterModal: false,
    };
  },
  components: {
    Card,
    SearchBar,
    Accordion,
    NavFilter,
    Logo,
    FilterModal,
  },
  methods: {
    aplicarFiltros(filtros, type) {
      if (type === 'ordenar') {
        this.filters[0].pop();
        this.filters[0].push(...filtros);
      }
      if (type === 'filtrar') {
        this.filters[1].splice(0, this.filters[1].length);
        this.filters[1].push(...filtros);
      }
      if (type === 'buscar') {
        this.filters[2].pop();
        this.filters[2].push(filtros);
      }
    },

    responsiveFilter(filtros) {
      this.filters = filtros;
    },

    ...mapActions({
      loadUsers: 'accountStore/loadUsers',
    }),
  },
  computed: {
    ...mapState({
      users: (state) => state.accountStore.users,
    }),

    ...mapGetters({
      filterUsers: 'accountStore/filterUsers',
    }),
  },
  created() {
    this.loadUsers();
  },

};
</script>

<style lang="scss" scoped>
@import '@/assets/styles/main';

.inicio {
  position: relative;
  display: flex;
  flex-flow: column;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    display: none;
  }
}

.nav {
  margin: 2em;
  display: flex;
  justify-content: space-between;
  * {
    width: 30%;
  }

  .filterComp {
    display: none;
  }
}

.cards-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  overflow-y: scroll;
  gap: 3em;
  margin: 2em 3em 3em;
  flex-grow: 1;
  &::-webkit-scrollbar {
    display: none;
  }
}
.no-cards {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;

  h2 {
    font-size: $text-lg;
    font-weight: $bold;
    color: $color-gray;
  }
}

@media (max-width: 1000px) {
  .cards-container {
    grid-template-columns: repeat(3, auto);
  }
  .search_bar {
    width: inherit;
  }
}

@media (max-width: 900px) {
  .logo {
    &--mobile-only {
      margin: 1.5em;
      margin-bottom: 0;
    }
  }
}

@media (max-width: 600px) {

  .nav {
    justify-content: start;
    margin: 1rem;

    .filterComp { display: flex; }
  }

  .cards-container {
    height: 60%;
    grid-template-columns: auto;
    gap: 1em;
    margin: 0.5em;
  }

  .accordion { display: none; }
}
</style>
