<template>
<div class="filterView">
  <nav>
    <div @click="emitReturn">
      <img src="../assets/arrow_left-ICON.png" alt="arrow-icon">
      <span>Volver</span>
    </div>
    <div @click="cleanFilters">
      <img src="../assets/filter_white-ICON.png" alt="filter-icon">
      <span>Limpiar filtros</span>
    </div>
  </nav>
  <div class="accordions">
    <Accordion v-on:emitirFiltros='guardarFiltros'
      :content="content1"
      type='filtrar'
      text = "Programa de Especializacion"/>
    <Accordion v-on:emitirFiltros='guardarFiltros'
      :content="content2"
      type='ordenar'
      text = "Ordenar por"/>
  </div>
  <button class="btn" @click="emitFilters">Aplicar Filtros</button>
</div>

</template>

<script>
import Accordion from '@/components/Accordion.vue';

export default {
  name: 'FilterView',
  data() {
    return {
      content1: [{ name: 'Front-End', id: 1 }, { name: 'Back-End', id: 2 }, { name: 'Desarrollo de APPS', id: 3 }, { name: 'Dise√±o UX', id: 4 }, { name: 'Desarrollo de Videojuegos en Unity', id: 5 }],
      content2: [{ name: 'A - Z', id: 6 }, { name: 'Z - A', id: 7 }],
      filters: [[], [], []],
    };
  },
  components: {
    Accordion,
  },
  methods: {
    guardarFiltros(filtros, type) {
      if (type === 'ordenar') {
        this.filters[0].pop();
        this.filters[0].push(...filtros);
      }
      if (type === 'filtrar') {
        this.filters[1].splice(0, this.filters[1].length);
        this.filters[1].push(...filtros);
      }
    },
    emitReturn() {
      this.$emit('emitReturn');
    },
    emitFilters() {
      this.$emit('emitirFiltros', this.filters);
      this.emitReturn();
    },
    cleanFilters() {
      this.$emit('emitirFiltros', [[], [], []]);
      this.emitReturn();
    },
  },
};
</script>

